const CharityBanner = props => {
  const { charity } = props;
  if (!charity) return null;
  const { url, image } = charity;
  if (!url) return null;

  return (
    <>
      <div>
        <img src={image} />
        <br />
        Extra-Life Team
      </div>
      <style jsx>
        {`
          img {
            width: auto;
            height: 50px;
          }
          div {
            text-align: center;
            background: #ffffff;
            color: #1d5b7d;
            border-radius: 20px 20px 0 0;
          }
        `}
      </style>
    </>
  );
};
export default CharityBanner;
